import React, {Component} from 'react';
import Button from '@material-ui/core/Button';
import LernappAPI from '../../API/LernappAPI';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

/**
 * Lschdialog fr das Lschen von Requests.
 *
 * @author [Khadidja Kebaili](https://github.com/KhadidjaKebaili)
 */


class DeleteRequest extends Component {
    constructor(props){
        super(props);

        this.state = {
            deletingInProgress: false,
            deletingError: null
            
        };
    }

    /**
     * Lscht einen Einzel-Request mithilfe der RequestID. Setzt whrend eines erfolgreichen Lschvorgangs
     * den deletingInProgress auf True, ansonsten verbleibend auf false wirft eine Fehlermeldung.
     */

    deleteRequest = () => {
        let id = this.props.request.getID()
        LernappAPI.getAPI().deleteRequest(id).then(request => {
            this.setState({  //Setzt neuen State, wenn der request gefetcht wurde.
                deletingInProgress: false, //Setzt Lschvorgang im State auf false.
                deletingError: null        //Fehlermeldung auf null setzen.
            })
            this.props.onClose(this.props.request);
        }).catch(e =>
            this.setState({
                deletingInProgress: false,//Bei fehlgeschlagenem Fetch-Vorgang wird Lschvorgang deaktiviert
                deletingError: e          //Fehler wird in den State gesetzt.
            })
        );

        // set loading to true
        this.setState({
            deletingInProgress: true, //Setzt Lschvorgang im State auf true.
            deletingError: null       //Fehlermeldung auf null setzen.
        });
    }



    deleteGroupRequest = () => {
        LernappAPI.getAPI().deleteRequest(this.props.requestGroup.getID()).then(request => {
            this.setState({  //Setzt neuen State, wenn der request gefetcht wurde.
                deletingInProgress: false, //Setzt Lschvorgang im State auf false.
                deletingError: null        //Fehlermeldung auf null setzen.
            })
            this.props.onClose(this.props.requestGroup);
        }).catch(e =>
            this.setState({// Reseten den State mit zurckgegebener Fehlermeldung
                deletingInProgress: false, // Ladevorgang wird auf false gesetzt
                deletingError: e          // Fehlermeldung wird zurckgegeben.
            })
        );

        this.setState({
            deletingInProgress: true,  //Setzt Lschvorgang im State auf true.
            deletingError: null        //Fehlermeldung auf null setzen.
        });
    }

    /**
     * Handelt den Abbruch des Lschvorgangs oder das Schlieen des Dialogs nach erfolgreichen Lschung.
     */
    handleClose = () => {
        this.props.onClose(null);
    }

    render() {
        return(
            <div>
                {/*Dialog der geffnet wird, wenn props.show auf True gesetzt wird. Beim Schlieen
                wird die handleClose-Methode ausgefhrt.*/}
                <Dialog open={this.props.show} onClose = {this.handleClose}>
                    <DialogTitle id="alert-dialog-title">Willst du wirklich die Anfrage lschen?</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            Wenn Sie auf Ablehnen klicken wird der Request fr immer gelscht
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button color="primary" onClick={this.handleClose}>
                            Abbrechen
                        </Button>
                        {/*Bei Bettigung des Buttons, wird die Methode deleteRequest ausgefhrt.*/}
                        <Button color="primary" onClick={this.deleteRequest}>
                            Ja, Ablehnen
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }}


export default DeleteRequest;