/**
 * Basisklasse fr alle BusinessObjects, fr die standardmig eine ID angegeben ist
 */
 export default class BusinessObject {

    /**
     * The null constructor.
     */
    constructor() {
      this.id = 0;
    }
  
    /**
     * Setzt ID des Businessobjekts.
     * 
     * @param {*} aId - neue ID fr Businessobjekt.
     */
    setID(aId) {
      this.id = aId;
    }
  
    /**
     * Gibt die ID des Businessobjekts zurck.
     */
    getID() {
      return this.id;
    }
  
    /**
     * Gibt einen representativen String des Objekts zurck.
     */
    toString() {
      let result = '';
      for (var prop in this) {
        result += prop + ': ' + this[prop] + ' ';
      }
      return result;
    }
  }